.warning-title h2{
    text-align: center;
    font-weight: 700;
    font-size: xx-large;
}
.warning-dialog{
    padding-bottom: 30px !important;
    font-size: large;
    font-family: sans-serif;
    text-align: center;
}
.warning-title.error h2{
    color: #FF5C5C;
}
.warning-title.warning h2{
    color: #E8B000;
}
.warning-title.info h2{
    color: #3F51B5;
}
.dialog-buttons{
    justify-content: center !important;
}
.dialog-buttons button.error{
    background: #FF5C5C;
    color: whitesmoke;
}
.dialog-buttons button.warning{
    background: #E8B000;
    color: whitesmoke;
}
.dialog-buttons button.info{
    background: #3F51B5;
    color: whitesmoke;
}