.item-card{
    margin: 10px 10px;
    width: calc(100% - 20px);
    border-radius: 8px;
    box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
}
.item-details{
    width: calc(100% - 70px);
    text-align: start;
    padding: 0 10px;
    font-size: 18px;
    font-style: italic;
    font-family: monospace;
    color: #6F6F6F;
}
.item-clear{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
}