


.row{
    display: flex;
    flex-wrap: wrap;
}

.justify-content-start{
    justify-content: flex-start;
}

.justify-content-center{
    justify-content: center;
}

.justify-content-end{
    justify-content: flex-end;
}

.align-items-start{
    align-items: start;
}

.align-items-center{
    align-items: center;
}

.align-items-end{
    align-items: end;
}

.col-3{
  width: 25%;
}

.col-6{
  width: 50%;
}

.col-9{
  width: 75%;
}

.col-12{
  width: 100%;
}

.col-sm-3, .col-sm-6, .col-sm-9, .col-sm-12,
.col-md-3, .col-md-6, .col-md-9, .col-md-12,
.col-xl-3, .col-xl-6, .col-xl-9, .col-xl-12{
  width: 100%;
}

@media screen and (min-width: 449px) {
  .col-sm-3{
    width: 25%;
  }
  .col-sm-6{
    width: 50%;
  }
  .col-sm-9{
    width: 75%;
  }
  .col-sm-12{
    width: 100%;
  }
  .col-md-3{
    width: 50%;
  }
  .col-md-6{
    width: 100%;
  }
  .col-md-9{
    width: 100%;
  }
}

@media screen and (min-width: 1099px) {
  .col-md-3{
    width: 25%;
  }
  .col-md-6{
    width: 50%;
  }
  .col-md-9{
    width: 75%;
  }
  .col-md-12{
    width: 100%;
  }
  .col-xl-3{
    width: 50%;
  }
  .col-xl-6{
    width: 100%;
  }
  .col-md-9{
    width: 100%;
  }
}

@media screen and (min-width: 1749px) {
  .col-xl-3{
    width: 25%;
  }
  .col-xl-6{
    width: 50%;
  }
  .col-xl-9{
    width: 75%;
  }
  .col-xl-12{
    width: 100%;
  }
}