.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 1s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@import url('./styles/GridStyle.css');
@import url('./styles/DivSize\'nSpace.css');

.title-wrapper{
  font-size: 2.75rem;
  font-weight: 700;
  margin: 1rem
}
.button-actions{
  gap: 1rem;
  display: flex;
  justify-content: center;
  padding-bottom: 10px;
}
.text-align-left{
  text-align: left;
}
.text-align-right{
  text-align: right;
}
.text-align-center{
  text-align: center;
}
@media screen and (max-width: 700px){
  .title-wrapper{
      font-size: 8vw;
  }
  .mobile-margin{
    margin-bottom: 60px;
  }
  .button-actions{
    position: fixed;
    bottom: 0;
    width: 100vw;
    left: 0;
    background: white;
    z-index: 1;
    padding-top: 10px;
    box-shadow: 5px 0px 10px 5px #b4b4b4;
  }
}